/*Image Picker---------------------*/

.ul-image-picker {
    list-style-type: none;
}

.li-image-picker {
    display: inline-block;
}

input[type="radio"][id^="cb"] {
    display: none;
}

.label-image-picker {
    border: 1px solid #fff;
    padding: 10px;
    display: block;
    position: relative;
    margin: 10px;
    cursor: pointer;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    user-select: none;
}

.label-image-picker::before {
    background-color: white;
    color: white;
    content: " ";
    display: block;
    border-radius: 50%;
    border: 1px solid grey;
    position: absolute;
    top: -5px;
    left: -5px;
    width: 25px;
    height: 25px;
    text-align: center;
    line-height: 28px;
    transition-duration: 0.4s;
    transform: scale(0);
}

.label-image-picker img {
    height: 100px;
    width: 100px;
    transition-duration: 0.2s;
    transform-origin: 50% 50%;
}

:checked+.label-image-picker {
    border-color: #ddd;
}

:checked+.label-image-picker::before {
    content: "✓";
    background-color: grey;
    transform: scale(1);
}

:checked+.label-image-picker img {
    transform: scale(0.9);
    box-shadow: 0 0 5px #333;
    z-index: -1;
}

.max-1000 {
    max-width: 1000px;
